"use client";

import WSButton from "@/app/UI/WSButton/WSButton";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@radix-ui/react-avatar";
import {
  ArrowRight,
  ArrowUpRight,
  ChevronRight,
  Circle,
  Link,
} from "lucide-react";

import { motion } from "framer-motion";
import { Fragment, useEffect, useState } from "react";
import ServiceCard from "@/app/components/ServiceCard";
import { Logos } from "./logos";
import Masonry from "react-masonry-css";
import { Card } from "@/components/ui/card";
import MasonaryCard from "@/app/components/Masonary";
import Experts from "@/app/components/Experts";
import { backgrounds } from "@/app/components/Backgrounds";
import { Tooltip, TooltipContent, TooltipTrigger } from "@/components/ui/tooltip";

export default function Hero() {
  // state for logo switch
  let [bgIndex, setBgIndex] = useState(0);

  // experts
  const [experts, setExperts] = useState("the composable stack.");
  // backgrounds
  const bg = backgrounds;

  const [animateMobile, setAnimateMobile] = useState("");
  useEffect(() => {
    const handleResize = () => {
      setAnimateMobile(window.innerWidth <= 768);
    };

    handleResize();
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, []);

  // const bg = '/images/Masonary/m-1.webp'

  return (
    <Fragment>
      {/* hero */}
      {bg.map((bg, index) => {
        if (bgIndex === index) {
          return (
            <section
              key={index}
              style={{ backgroundImage: `url(${bg.bg})` }}
              className={`min-h-screen bg-${bg.bgColor} bg-cover bg-center bg-no-repeat flex flex-col items-center justify-center pb-[20px] md:px-[5%] md:px-12`}
            >
              <div className="  w-full flex max-md:flex-row  h-[100vh] grid md:grid-cols-2  items-center">
                {/* Left Text Content */}
                <div className=" flex flex-col max-md:h-1/2  h-2/3 justify-evenly px-[5%]">
                  <h1
                    className={`text-5xl max-md:text-[24px] font-bold leading-tight text-${bg.textColor} `}
                  >
                    We craft fast, scalable sites for teams of all sizes.
                  </h1>

                  <h4
                    className={`font-normal text-${bg.textColor} text-[20px] max-md:text-[16px] md:leading-[30px] `}
                  >
                    Webstacks empowers marketing teams to break free from
                    website bottlenecks with a composable approach built for
                    speed and scale.
                  </h4>

                  <div className="flex max-md:flex-col gap-4 max-md:w-full">
                    <WSButton
                      icon={<ArrowUpRight />}
                      value="Book Intro Call"
                    ></WSButton>
                    <Button className="md:p-7 text-base flex items-center gap-2 md:text-[18px] max-md:text-[14px] max-md:p-3">
                      View Client Stories
                      <ArrowRight className="w-5 h-5 max-md:w-2 max-md:h-2 text-white" />
                    </Button>
                  </div>
                </div>

                {/* Right Image */}
                <motion.div
                  initial={{ rotateX: 0, rotateY: 0 }}
                  animate={{ rotateX: 360, rotateY: 360 }}
                  transition={{ duration: 2, ease: "easeInOut" }}
                  className=" border   rounded-[10px] h-[80%] max-md:h-[80vh] max-md:mt-20 bg-white "
                >
                  <div className=" flex flex-row text-[10px] h-[36px] border-b-1 justify-between items-center bg-white px-4 rounded-t-sm">
                    <div className="flex flex-row space-x-1 ">
                      <Circle
                        className={`md:w-4 md:h-4 max-md:w-3 max-md:h-3 bg-gray-200 text-gray-200 rounded-[10px]`}
                      />
                      <Circle className="w-4 h-4 max-md:w-3 max-md:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                      <Circle className="w-4 h-4 max-md:w-3 max-md:h-3 bg-gray-200 text-gray-200 rounded-[10px]" />
                    </div>
                    <div className="flex justify-center   w-full">
                      <Link />
                    </div>
                  </div>
                  <div className="overflow-hidden max-md:h-[70vh] w-full flex flex-col justify-center  scrollbar-hide">
                    <Avatar className="h-110">
                      <motion.img
                        src="/images/heroScrolls/scroll-1.jpg"
                        alt="Scrolling image"
                        className="w-full h-auto"
                        initial={{ y: 0 }}
                        animate={animateMobile ? { y: 0 } : { y: -2400 }}
                        transition={{
                          duration: 10,
                          ease: "easeInOut",
                          repeat: Infinity,
                          repeatType: "reverse",
                        }}
                      />
                    </Avatar>
                  </div>
                </motion.div>
              </div>
              <div className="item-start max-md:mt-80   w-full border-b-1 pb-[10px]">
                <h4 className={`text-[20px] text-${bg.textColor}`}>
                  Trusted by the world's fastest-growing{" "}
                  <span
                    className={`font-semibold text-${bg.textColor} cursor-pointer`}
                  >
                    Startups
                  </span>{" "}
                  and{" "}
                  <span
                    className={`font-semibold text-${bg.textColor} cursor-pointer`}
                  >
                    Enterprises
                  </span>
                </h4>
              </div>
              <div className="flex flex-row flex-wrap  justify-between  w-full">
                {Logos.map((item, index) => {
                  return (
                    <div
                      key={index}
                      className=" flex flex-col max-md:w-1/2 flex-col items-center    "
                    >
                      <Avatar>
                        <AvatarImage
                          src={item.logo}
                          className=" w-20 h-20 object-contain grayscale "
                          onClick={() => {
                            setBgIndex(index);
                          }}
                        />
                        <AvatarFallback>Logo here</AvatarFallback>
                      </Avatar>
                      <Circle className="w-2 h-2 bg-gray-400 text-gray-200 rounded-[10px]  " />
                    </div>
                  );
                })}
              </div>
            </section>
          );
        }
      })}

      {/* Services */}
      <section className="  flex flex-col items-center justify-center bg-black ">
        <div className="container  flex justify-center pt-[110px]">
          <div className="upper text-center w-[72%]  space-y-8">
            <h2 className="text-[60px]  max-md:text-[24px] text-center font-bold leading-tight text-white">
              Websites aren’t just part of what we do. They’re everything we do.
            </h2>
            <p className="text-[#a5acc0] text-center max-md:text-[14px] text-[20px] leading-[30px]">
              We help B2B marketing teams turn outdated, underperforming
              websites into scalable, high-converting products that engage
              audiences, generate pipeline, and accelerate growth.
            </p>
          </div>
          <div className="lower"></div>
        </div>

        <div className="w-full pt-[200px]">
          <ServiceCard />
        </div>
      </section>

      {/* Stories */}
      <section className="  flex flex-row max-md:flex-col items-center h-[80vh] max-md:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <div className="md:left w-1/2">
          <h2 className="text-white text-[60px] max-md:text-[24px] md:leading-[70px] ">
            Brands you know.
            <br /> Results you’ll love.
          </h2>
        </div>
        <div className="right flex flex-col max-md:justify-center max-md:text-center max-md:items-center ">
          <h4 className="text-[#a5acc0] text-[20px] max-md:text-[16px]  pb-10">
            Real stories from companies that wanted more—and got it.
          </h4>
          <div>
            <WSButton
              value="View Client Stories"
              icon={
                <ChevronRight className="absolute inset-0 transition-opacity duration-200 opacity-100 group-hover:opacity-0" />
              }
              hovericon={
                <ArrowRight className="absolute inset-0 transition-opacity duration-300 opacity-0 group-hover:opacity-100" />
              }
            />
          </div>
        </div>
      </section>

      {/* masnory */}
      {/* <Masonry
        breakpointCols={{ default: 4, 1024: 3, 768: 2, 500: 1 }}
        className="flex flex-row gap-4"
        columnClassName="space-y-4"
      >

          <MasonaryCard />
 
      </Masonry> */}

      {/* Experts */}
      <section className="  flex flex-row max-md:flex-col items-center h-[30vh] max-md:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <Experts />
      </section>

      {/* industries */}
      <section className="  flex flex-col max-md:flex-col items-center h-[80vh] max-md:h-[100vh] px-[5%] justify-evenly bg-black   ">
        <div className="md:left w-[70%]">
          <h2 className="text-white text-[60px] text-center max-md:text-[24px] md:leading-[70px] ">
            Serving industries that demand speed and scale.
          </h2>
        </div>
        <div className="right flex flex-col max-md:justify-center max-md:text-center items-center ">
          <h4 className="text-[#a5acc0] text-[20px] max-md:text-[16px]  pb-10">
            We specialize in industries where high performance matters most.
          </h4>
          <div>
            <WSButton
              value="View All Industries"
              icon={
                <ChevronRight className="absolute inset-0 transition-opacity duration-200 opacity-100 group-hover:opacity-0" />
              }
              hovericon={
                <ArrowRight className="absolute inset-0 transition-opacity duration-300 opacity-0 group-hover:opacity-100" />
              }
            />
          </div>
        </div>
      </section>

      <section className="  flex flex-col max-md:flex-col items-center h-[80vh] max-md:h-[100vh] px-[5%] justify-evenly bg-black   ">
        
      </section>
    </Fragment>
  );
}
